

export const getMedicationDispense = ( obj, labels, Data ) => {

     Data[`${labels[0][0]}`] =  Data[`${labels[0][0]}`]
                                   ? [...Data[`${labels[0][0]}`] ]
                                   : [] ;

     const auxObj = {} ;

     auxObj[`${labels[1][0]}`] = obj.medicationReference.display ;
     auxObj[`${labels[1][1]}`] = obj.category.text ;
     auxObj[`${labels[1][2]}`] = `${obj.quantity.value} ${obj.quantity.unit}` ;
     auxObj[`${labels[1][3]}`] = `${obj.daysSupply.value !== 0 ? obj.daysSupply.value : ''} ${obj.daysSupply.unit}` ;
     auxObj[`${labels[1][4]}`] = obj.whenHandedOver ;


     Data[`${labels[0][0]}`].push( auxObj ) ;

     return ;
};
