

export const getPatient = ( obj )  => {

     return(
          {
               'Cuenta corriente' :  obj.identifier[0].value,
               'Edad' : new Date().getFullYear() -new Date(obj.birthDate).getFullYear(),
               'Nombres y apellidos' : obj.name[0].text,
               'Sexo' : obj.gender === 'male' ? 'Masculino' : 'Femenino',
               'Estado de bienestar (0-3)' : obj.extension[0].valueString,
               'Cama' : obj.address[0].text,
               'Ala' : obj.address[0].district,
               'Sala' : obj.address[0].state,
               'Foto' : {content:obj.photo, state:false}
          }
     )
};
