

export const getBasic_examen = ( obj, labels, Data ) => {

     const auxObj = {};
     auxObj[`${labels}`] = ' ';

     if( obj.code?.coding ){
          auxObj[`${labels}`] = obj.code.coding.map((item)=>{
               return( item.display );
          })
     }


     Object.assign( Data, { ...auxObj } )

     return ;

};
