     import { CheckResourceExist } from './CheckResourceExist' ;

export const Bundle_request = async( idPatient, tablas, date ) => { // date : "2022-12-19"

     // const addCero = (num)=>{
     //      if(num >=10) return num
     //      return `0${num}`
     // };
     
     let today = `${date}T00:00:00Z` ;
     // let tomorrow = date.split('-').reduce( (acc,_,__,arr) => acc=`${arr[0]}-${arr[1]}-${ addCero(parseInt(arr[2])+1) }T00:00:00Z`) ;

     const bundle = {
          resourceType : "Bundle",
          type : "batch",
          entry:[]
     };

     if( tablas.resources ){
          tablas.resources.forEach( resources => {
               tablas.fields.forEach( fields => {
               bundle.entry.push(
                         {
                              "request" : {
                                   "method" : "GET",
                                   "url" : !resources?.daily 
                                             ? `${resources.resource}?subject=Patient/${idPatient}&identifier=${resources.identifier}:${fields.replace(/ /g,'').replace(/[^a-zA-Z 0-9 ]/g, "")}`
                                             : `${resources.resource}?subject=Patient/${idPatient}&identifier=${resources.identifier}:${fields.replace(/ /g,'').replace(/[^a-zA-Z 0-9 ]/g, "")}&identifier=${today}`
                              }
                         }
                    );
               });
          });
     }else{
          tablas.forEach( obj => {
               bundle.entry.push(
                    {
                         "request" : {
                              "method" : "GET",
                              "url" :  ! obj?.daily 
                                        ? `${obj.resource}?subject=Patient/${idPatient}&identifier=${obj.identifier}`
                                        : `${obj.resource}?subject=Patient/${idPatient}&identifier=${obj.identifier}&identifier=${today}`
                                        
                         }
                    }
               );
          });
     };

     const array = await CheckResourceExist( ``, true, 'POST', bundle );

     return array ;
};
