
import { resourceServer }  from  '../../../uci/env' ;



export const getBundle = async( resource ) => {

     const { urlServer, requestOptionsGet } = resourceServer ;

     let resp = await fetch(`${urlServer}${resource}`, requestOptionsGet ) ;
     resp = await resp.json()

     if( resp.total >= 10 ){
          resp = await fetch(`${urlServer}${resource}&_count=${resp.total}`, requestOptionsGet ) ;
          resp = await resp.json()
     }

     return resp ;
};
