
import { addToken } from '../../../uci/components/helpers/token';
import { resourceServer }  from  '../../../uci/env' ;

const { urlServer, requestOptionsGet, requestOptionsPost } = resourceServer ;

export const CheckResourceExist = async( resource='', returnObj=false, verb='GET', objPost={} ) => {

     let resp = {};

     if( verb === 'GET'){
          resp = await fetch(`${urlServer}${resource}`, addToken(requestOptionsGet ) )
     }else if( verb === 'POST' ){

          requestOptionsPost.body =  JSON.stringify( objPost ) ;

          const config = addToken(requestOptionsPost) ;
          config.headers.append("Content-Type", "application/json") ;

          resp = await fetch(`${urlServer}${resource}`,  config ) ;
     };

     const objGet = await resp.json();

     if( objGet.total > 0 | (objGet.entry ? objGet.entry.length > 0 : false) ){
          return(
               returnObj ?
                    verb==='GET' ?
                         objGet.entry[0].resource
                         : objGet.entry
                    : true
          )
     };
     return false
};
