

export const getBasic_programacion = ( obj, labels, Data ) => {

     const auxObj = {};
     auxObj[`${labels}`] = ' ' ;

     if( obj?.code?.coding ){
          auxObj[`${labels}`] = obj.code.coding.map((item)=>{
               return( item.display );
          }) ;
     }
     
     Object.assign( Data, { ...auxObj } )

     return ;
};
