


export const getBasic = ( obj, labels, Data, isArray=false ) => {

     if( Array.isArray( labels[0] ) ){

          if( isArray ){

               Data[`${labels[0][0]}`] = Data[`${labels[0][0]}`] ?
                                        [...Data[`${labels[0][0]}`]]
                                        : [] ;

               const auxObj = {} ;
               auxObj[`${labels[1]}`] = ' ' ;

               if( obj.code?.coding ){
                    auxObj[`${labels[1]}`] = obj.code.coding.map((item)=>{
                         return( item.display ) ;
                    });

               }

               Data[`${labels[0]}`].push(
                    auxObj
               );

               return ;

          }else{

               Data[`${labels[0][0]}`] =  Data[`${labels[0][0]}`] ? {...Data[`${labels[0][0]}`]} : {} ;

               labels.slice(1,).forEach( ( item,idx ) => {
                    Data[`${labels[0][0]}`][`${labels[0][1]}`] = {...Data[`${labels[0][0]}`][`${labels[0][1]}`],
                         [`${item}`] : obj.code.coding[idx]?.display  === '--' ? '' : obj.code.coding[idx]?.display }
               });
               return ;
          };

     }else{
          const arrayObjs = {};
          let tempObj = {} ;
          labels.forEach( ( item,idx ) => {
               tempObj = {  [`${item}`] : obj.code.coding[idx].display  === '--' ? '' : obj.code.coding[idx].display };
               if( obj.code.coding[idx].version ){
                    tempObj[`${item}_version`] = obj.code.coding[idx].version  === '--' ? '' : obj.code.coding[idx].version ;
               };
               Object.assign( arrayObjs, { ...tempObj } );
          }) ;
          return(
               {
                    ...arrayObjs
               }
          );
     };
};
