


export const getDiagnosticReport = ( obj, args ) => {

     return(
          {
               [`${args[0]}`] : obj.resultsInterpreter[0].display === '--' ? '' : obj.resultsInterpreter[0].display,
               [`${args[1]}`] : obj.result[0].display === '--' ? '' : obj.result[0].display,
               [`${args[2]}`] : obj.conclusion === '--' ? '' : obj.conclusion
          }
     );
};
