

export const getBasic_hidra = ( obj, labels, Data ) => {

     Data[`${labels[0][0]}`] = Data[`${labels[0][0]}`] ?
                              { ...Data[`${labels[0][0]}`] }
                              : {} ;

     const auxObj = {} ;
     auxObj[`${labels[1]}`] = 0 ;

     if( obj?.code?.coding ){
          auxObj[`${labels[1]}`] = obj.code.coding.map((item) => {
               return( item.display ) ;
          }) ;
     } ;


     Object.assign( Data[`${labels[0][0]}`], { ...auxObj } ) ;

     return ;

};
