

export const getBasic_ingresos = ( obj, labels, Data ) => {

     Data['BalanceDÍA'] =  Data['BalanceDÍA'] ?
                         [...Data['BalanceDÍA'] ]
                         : [] ;

     const auxObj = {} ;

     obj.code.coding.forEach((item,idx) => {
          auxObj[`${labels[idx]}`] = item.display 
     });

     Data['BalanceDÍA'].push( auxObj ) ;

     return ;
};
