

import { resourceServer }  from  '../../uci/env' ;
import { CheckResourceExist } from './helpers/CheckResourceExist' ;
import { Bundle_request } from './helpers/Bundle_request' ;


import { getAccount } from './helpers/getAccount' ;
import { getObservation } from './helpers/getObservation' ;
import { getDiagnosticReport } from './helpers/getDiagnosticReport' ;
import { getCommunication } from './helpers/getCommunication' ;
import { getBasic } from './helpers/getBasic' ;
import { getBasic_examen } from './helpers/getBasic_examen' ;
import { getBasic_programacion } from './helpers/getBasic_programacion' ;
import { getBasic_hidra } from './helpers/getBasic_hidra' ;
import { getBasic_ingresos } from './helpers/getBasic_ingresos' ;
import { getMedicationDispense } from './helpers/getMedicationDispense' ;
import { getPatient } from './helpers/getPatient' ;
import { getBundle } from './helpers/getBundle' ;

export const getFromServer = async ( cnt_patient, date  ) => {

     const { urlServer, requestOptionsGet } = resourceServer ;

     // medir el tiempo de procesado de las solicitudes
     let t1 = new Date();

     const patGet = {};
     patGet.respGet = await fetch( `${urlServer}Patient?identifier=${cnt_patient}`, requestOptionsGet )
                         .catch( err => console.log( err, `${urlServer}Patient?identifier=${cnt_patient}`) ) ;
     patGet.objGet = await patGet.respGet.json();

     let  idPatient // = "1816d456235-8da18d0b-3ec6-4a5f-b794-f246466e1ce0";
     if( await patGet.objGet.total > 0  ){ // Verificar si existe el paciente: Los objetos se actualizan
          idPatient =  patGet.objGet.entry[0].resource.id ;
          console.log( "Paciente encontrado, id: ", idPatient );
     }else{
          // No existe un paciente con esa cuenta corriente, no se crea nada.
          return(-1)
     };

     // const bundle = {
     //      resourceType : "Bundle",
     //      type : "batch",
     //      entry:[]
     // };

     const data = getPatient( patGet.objGet.entry[0].resource );

     const resources = [
          { resource:'Account', identifier:`Account`, args:['Fecha','Año','Mes','Día','RUT','UCI'], daily:false },
          { resource:'Basic', identifier:`diasRegistro`, args:['Días Hosp.','Días UPC','Días postOp.'], daily:false },
          { resource:'Observation', identifier:`Rh-Bld`,args: ['valueString', ['Rh']], daily:false },
          { resource:'Observation', identifier:`body-weigth`,args:['valueQuantity', ['Peso'] ], daily:false },
          { resource:'Observation', identifier:`talla-cm`,args:['ValueInteger', ['Talla'] ], daily:false},
          { resource:'DiagnosticReport', identifier:`clinicInfo`,args:['Motivo Consulta','Estado','Diagnostico'], daily:false },
          { resource:'Communication', identifier:`equipoTrt`,args:['Equipo tratante'], daily:false },
          { resource:'Basic', identifier:`obsEvolucion`, args:['Cardiovascular-Día','Respiratorio-Día','Neurologíco-Día','Eliminación-Día','Renal-Día','Alimentación-Día','Post-Quirúrgico-Día','Cardiovascular-Noche','Respiratorio-Noche','Renal-Noche','Neurologíco-Noche','Alimentación-Noche','Eliminación-Noche','Post-Quirúrgico-Noche'], daily:true },
          { resource:'Basic', identifier:`brazaleteIdentificacion`, args:[ 'Turno Largo','Turno Noche'], daily:true },
          { resource:'Basic', identifier:`brazaleteAlergias`, args:[ 'Brazalete de Alergías','Alérgeno'], daily:false },
          { resource:'Basic', identifier:`riesgoEnfTrombo`, args:['Riesgo Enf. Tromboembólica'], daily:false },
          { resource:'Basic', identifier:`indMedicas`, args:[ 'Aislamiento','Reposo','Régimen','Profiláxis Mecánica (TEV)','Especifique','KTR','Destete'], daily:false },
          { resource:'Basic', identifier:`Medicacion`, args:[ 'Profilaxis Farmacologica','Contenciones','Nombre Doctor','Código'], daily:false },
          { resource:'Basic', identifier:`MantencionDisp.`, args:['TOT','TQT','CVC','CUP','MPT','CF','CHD','CSG','SNG','SNE','SNY','LA','VVP','CPD','S.Foley','Drenajes'], daily:false },
          { resource:'Communication', identifier:`alarmaDia-resp`,args:['Responsable (DÍA):'], daily:true },
          { resource:'Basic', identifier:`alarmaDia-hor`, args:['Hora (DÍA):'], daily:true },
          { resource:'Basic', identifier:`alarmaNoche-resp`, args:['Responsable (NOCHE):'], daily:true },
          { resource:'Basic', identifier:`alarmaNoche-hor`, args:['Hora (NOCHE):'], daily:true },
          { resource:'Basic', identifier:`examenFiscNombTurnLarg`, args:['NOMBRE: Enfermero Turno Largo'], daily:true },
          // { resource:'Basic', identifier:`examenFiscParam`, args:['TOT','TQT','CVC','MPT','CF','CSG','SNG','SNY','LA','VVP','CPD','S.Foley','Drenajes'] },
          { resource:'Basic', identifier:`examenFiscNombTurnNoct`, args:['NOMBRE: Enfermero Turno Nocturno'], daily:true },
          { resource:'Basic', identifier:`ProgramacionEnferIzq`, args:[['ProgramacionEnfer','izq'], 'BD','BI','PD','PI','Tórax','NA','NOMBRE: Enfermero Turno Diurno'], daily:true },
          { resource:'Basic', identifier:`ProgramacionEnferDer`, args:[['ProgramacionEnfer','der'], 'BD','BI','PD','PI','Tórax','NA','NOMBRE: Enfermero Turno Nocturno'], daily:true },
          { resource:'Basic', identifier:`riesgoCaidaLargo`, args:[['Riesgo de Caída','evalLargo'], 'Puntaje','medida'], daily:true },
          { resource:'Basic', identifier:`riesgoCaidaNoche`, args:[['Riesgo de Caída','evalNoche'], 'Puntaje','medida'], daily:true },
          { resource:'Basic', identifier:`riesgoUPPLargo`, args:[['Riesgo de UPP','evalLargo'], 'Puntaje','medida'], daily:true },
          { resource:'Basic', identifier:`riesgoUPPNoche`, args:[['Riesgo de UPP','evalNoche'], 'Puntaje','medida'], daily:true },
     ] ;

     let array_bundle = await Bundle_request( idPatient, resources, date );

     console.log( "Primer grupo de datos: ", array_bundle );

     array_bundle.forEach( (item,idx)=>{
          if( item.resource.total >= 1  ){
               // carga el ultimo recurso, en caso de duplicidad se toma el más actualizado
               switch( item.resource.entry[item.resource.entry.length-1].resource.resourceType ) {
                    case 'Account':
                         Object.assign( data, getAccount( item.resource.entry[0].resource, resources[idx].args ) );
                         break;
                    case 'Basic':
                         if( Array.isArray(resources[idx].args[0]) ){
                              getBasic( item.resource.entry[0].resource, resources[idx].args, data )
                         }else{
                              Object.assign( data, getBasic( item.resource.entry[0].resource, resources[idx].args, {}) );
                         };
                         break;
                    case 'Observation':
                         Object.assign( data, getObservation( item.resource.entry[0].resource, resources[idx].args ) );
                         break;
                    case 'DiagnosticReport':
                         Object.assign( data, getDiagnosticReport( item.resource.entry[0].resource, resources[idx].args ) );
                         break;
                    case 'Communication':
                         Object.assign( data, getCommunication( item.resource.entry[0].resource, resources[idx].args ) );
                         break;
                    default:
                         break;
               };
          };
     });


     const tablasAlarmas = {
          resources :[
               {resource:'Basic', identifier:'alarmaDia', args:['DÍA'] } ,
               {resource:'Basic', identifier:'alarmaNoche', args:['NOCHE'] }
          ],
          fields :[
               'Hora','T°C','FC','Código','P/A','PAM','PVC','PAP','PCP','Ángulo de Cabecera','EVA/S.A.S',
               'FR','Cód./Modo','FIO2','IPAP/PS','EPAP/PEEP','PC','VOL.CTE','VOL.MIN','SAT','CUFF/CMS',
               'DU Parcial','DU Total','Deposiciones','HGT','Insulina','Residuo Gástrico','Evaluación contenciones',
          ]
     } ;

     array_bundle = await Bundle_request( idPatient, tablasAlarmas, date );

     console.log( array_bundle );
     console.log( array_bundle.map( (arr,id) => {
          return(
               id,
               arr.resource.total > 0
                    ? arr.resource.entry[0].resource
                    : arr.resource
          )

     } ) );

     // Objeto que alojará las alarmas
     data.alarmas =  data.alarmas ?
                         {...data.alarmas}
                         :{} ;

     array_bundle.forEach( (item,idx) => {
          if( item.resource.total >= 1 ){

               if( idx < tablasAlarmas.fields.length  ){
                    getBasic( item.resource.entry[0].resource, [ tablasAlarmas.resources[0].args, tablasAlarmas.fields[idx] ], data.alarmas, true ) ;
               }else{
                    getBasic( item.resource.entry[0].resource, [ tablasAlarmas.resources[1].args, tablasAlarmas.fields[idx-tablasAlarmas.fields.length] ], data.alarmas, true ) ;
               };
          };
     });

     const tablasExamen = {
          resources:{ resource: 'Basic', identifier:'examenFisico' },
          fields :[
          //fieldsDia
               'Estado general','Conciencia','Act. Motor','Comportamiento','Piel','Cabeza','Cuello','Conjuntiva','Escleras','Pupilas',
               'F. Nasales','Labios','Lengua','Dentadura','Yugurales','Tóraz','Abdomen',
          //fieldsNoche
               'Región Lumbar','Sacro','Genitales','Región Perianal','ESD','ESI','EID','EII','Protecciń zonas de apoyo','N° TOT/cm','VM'
          ]
     };

     // Objeto que alojará los examen fisicos
     data['examen fisico'] =  data['examen fisico'] ?
          {...data['examen fisico']}
          :{} ;

     array_bundle = await Bundle_request( idPatient, {
          resources: [tablasExamen.resources],
          fields: tablasExamen.fields },
          date
     );

     array_bundle.forEach( (item,idx) => {
          if( item.resource.total >= 1 ){
               getBasic_examen( item.resource.entry[0].resource, tablasExamen.fields[idx] , data['examen fisico'] ) ;
          };
     });



     const tablasProgramacion = {
          resources:[
               { resource: 'Basic', identifier:'ProgramacionEnfer',args:['ProgramacionEnfer'] }
          ],
          fields: [
               'Aseo y Confort','Aseo Matinal','Baño en Cama','Lavado de Pelo','Aseo Genital','Aseo Cavidades','Aseo Ocular',
               'Prevención UPP','Colchón ant-esc.','Movilización','Riesgo Medio(4h)','Riesgo alto(2h)','Medidas Aisl.','Baño Clorex',
               'Aseo Bucal','Procedimientos','Curación CVC','Cms0','Sitio de inserción','Puntos de fijación','UPP','Ubicación','Grado',
               'Instalación Dispositivos','VVP/LA/SF','Cms1','Cms2','Cms3' //,'Izq','Der'
          ]
     };

     array_bundle = await Bundle_request( idPatient, tablasProgramacion, date );

     // Objeto que alojará la ProgramacionEnfer
     data['ProgramacionEnfer'] =  data['ProgramacionEnfer'] ?
                         {...data['ProgramacionEnfer']}
                         :{} ;
     array_bundle.forEach( (item,idx) => {
          if( item.resource.total >= 1 ){
               getBasic_programacion( item.resource.entry[0].resource, tablasProgramacion.fields[idx], data['ProgramacionEnfer'] ) ;
          };
     });

     const tablasHidratacion = {
          resources:[
                    { resource: 'Basic', identifier:'balancHidEgresos', args:['Egresos'] },
                    { resource: 'Basic', identifier:'balancHidIngresos', args:['Ingresos'] }
          ],
          fields: [
               [ // fieldsEgresos
                    'Diuresis','Deposiciones','SNG','Vómitos','Drenaje 1','Drenaje 2','Pérdidas Ins.','Ostomía',
                    'Exudado/ Asp','Total','Balance Acumulado'
               ],
               [ // fieldsIngresos
                    'Agua-Liquidos','Alimentos VO/Ent','Agua Metabólica','Dilución Fármaco','NPTC','Sangre/ Plasma',
                    'Drogas/ Flash','Sueros 1','Sueros 2','Analgecia','Total','Balance Parcial'
               ],
          ]
     };

     data['balance hidrico'] =  data['balance hidrico'] 
                                   ? {...data['balance hidrico']}
                                   : {} ;

     for( let idxResources=0 ; idxResources<2 ; idxResources++) {
          array_bundle = await Bundle_request( idPatient, {
               resources: [tablasHidratacion.resources[idxResources]],
               fields: tablasHidratacion.fields[idxResources]},
               date
          );

          array_bundle.forEach( (item,idx) => {
               if( item.resource.total >= 1 ){
                    getBasic_hidra( item.resource.entry[0].resource, [ tablasHidratacion.resources[idxResources].args, tablasHidratacion.fields[idxResources][idx] ], data['balance hidrico'] ) ;
               };
          });
     };

     const tablasIngresos = {
          resources:[
                    { resource: 'Basic', identifier:'ingresosDia', args:['BalanceDÍA'] },
          ],
          fields: [
               'Hora','Ing. oral/enteral','Cantidad','Inicio','Terminó','Pediendiente','Ing. parental', 'fecha'
          ]
     };

     let n = 0 ;
     do{
          let obj = await CheckResourceExist(`${tablasIngresos.resources[0].resource}?subject=${idPatient}&identifier=${tablasIngresos.resources[0].identifier}:${n}`, true);
          if( obj !== false ){
               getBasic_ingresos( obj, tablasIngresos.fields, data ) ;
          }else{
               break ;
          };
          n++ ;
     }while(1);

     const tablaMedicacion = {
          resources:[
               { resource: 'MedicationDispense', identifier:'Medicacion', args:['indicaciones'] },
          ],
          fields:['Medicamentos','Vía','Dosis','Frecuencia','fecha']
     };

     let obj = await getBundle(`${tablaMedicacion.resources[0].resource}?subject=${idPatient}`);
     if( obj.total > 0 ){
          obj.entry.forEach( (entry) => {
               getMedicationDispense( entry.resource, [ [tablaMedicacion.resources[0].args], tablaMedicacion.fields], data ) ;
          });
     };

     // console.log( "data: ", data );
     let t2 = new Date();
     console.log( "Tiempo : ", t2-t1, "ms" );

     return({  data, idPatient  });
};
