

export const getAccount = ( obj, args )  => {

     const cnt = JSON.parse(localStorage.getItem('cntPatient') ) ;
     const pat = JSON.parse(localStorage.getItem('patients') ).filter( pat => pat.cnt === cnt )[0] 

     return(
          {
               [`${args[0]}`] :  obj.identifier[0].period.start,
               [`${args[1]}`] :  new Date(obj.identifier[0].period.start).getFullYear(),
               [`${args[2]}`] :  new Date(obj.identifier[0].period.start).getUTCMonth()+1,
               [`${args[3]}`] :  new Date(obj.identifier[0].period.start).getUTCDate(),
               [`${args[4]}`] :  obj.name,
               [`${args[5]}`] :  obj.description === '--' ? `${pat.sala} - ${pat.cama}` : obj.description,
          }
     );
};
