

export const getObservation = ( obj, args )  => {

     let value ;
     switch( args[0] ) {
          case 'valueString':
               value = obj.valueString === '--' ? '' : obj.valueString ;
               break;
          case 'valueQuantity':
               value = obj.valueQuantity.value === '--' ? '' : obj.valueQuantity.value ;
               break;
          case 'ValueInteger':
               value = obj.valueInteger === '--' ? '' :  obj.valueInteger ;
               break;

          default:

     }

     return(
          {
               [`${args[1][0]}`] : value
          }
     );
};
